



























































































































.row-content {
  margin: 1.5rem auto;
  width: 100%;
  &__row {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    .label {
      font-size: 1.2em;
      //padding: 0 80px 0 0;
    }
    .input-field {
      display: flex;
      justify-content: center;
      align-items: center;
      &__input {
        width: 131px;
        height: 50px;
        text-align: center;
      }
      &__unit {
        margin: 0 8px;
      }
    }
  }
}
